export enum TooltipServiceEvents {
    /* Dispatched on window by service to trigger show/hide in dialog manager. For manager only! */
    Show = 'sui-tooltip-show',
    Hide = 'sui-tooltip-hide',

    /* Dispatched to self from manager when hover/unhover tooltip*/
    Hover = 'hover',
    Unhover = 'unhover',

    /* Dispatched on self when open/closed. For client to use! */
    Opened = 'open-tooltip',
    Closed = 'close-tooltip',
}
