import { css, CSSResultArray, cssSelector, cssVariableAsVar, GenerateStyles, style } from '@umetrics/sartorius-ui-base';

const cssNoMin = css; // html-minifier-terser fail!

export const tooltipStyles: GenerateStyles = (tagName) => {
    const { child, is } = cssSelector(tagName);

    const white = cssVariableAsVar('color_primary_2');
    const black = cssVariableAsVar('color_primary_3');

    return [
        style,
        cssNoMin`
            :host,
            :host::before,
            :host::after,
            :host *,
            :host *::before,
            :host *::after {
                box-sizing: border-box;
            }

            :host {
                display: inline-block;
                display: flex;
            }

            :host([manager]),
            :host([controlled]) {
                display: inline-block;
                position: fixed;
            }

            ${is('manager')} {
                width: 100%;
                height: 100%;
            }

            ${child('container')} {
                display: flex;
                align-items: center;
                flex-direction: column-reverse;
                position: relative;
            }

            ${is('manager')} ${child('container')} {
                width: 100%;
                height: 100%;
            }

            ${child('wrapper')} {
                visibility: hidden;
                opacity: 0;

                position: absolute;
                display: flex;
                flex-direction: column;
                z-index: var(--tool-tip-z-index, 2000);
                width: max-content;
            }

            ${child('arrow-left')} {
                width: 0;
                height: 0;
                border-style: solid;
                border-width: 7px 0 0 8px;
                border-color: ${black} transparent transparent transparent;
            }

            ${child('arrow-right')} {
                width: 0;
                height: 0;
                border-style: solid;
                border-width: 7px 8px 0 0;
                border-color: ${black} transparent transparent transparent;
            }

            ${child('message')} {
                font-size: 12px;
                background-color: ${black};
                color: ${white};
                margin: 0;
                padding: 8px 10px;
            }

            ${is('!own-width')} ${child('message')} {
                max-width: 200px;
            }

            ${child('top-right')} {
                align-items: flex-start;
                bottom: 100%;
                left: 50%;
                margin-bottom: 5px;
            }

            ${child('top-left')} {
                align-items: flex-end;
                bottom: 100%;
                right: 50%;
                margin-bottom: 5px;
            }

            ${child('container-bottom-right')} {
                flex-direction: column;
            }

            ${child('bottom-right')} {
                flex-direction: column-reverse;
                top: 100%;
                left: 50%;
                margin-top: 0px;
            }

            ${child('bottom-right')} ${child('arrow-left')} {
                transform: rotate(180deg);
            }

            ${child('bottom-left')} {
                flex-direction: column-reverse;
                align-items: flex-end;
                top: 100%;
                right: 50%;
                margin-top: 0px;
            }

            ${child('bottom-left')} ${child('arrow-right')} {
                transform: rotate(180deg);
            }

            ${child('bottom-center')} {
                flex-direction: column-reverse;
                top: 100%;
                left: 50%;
                margin-top: 0px;
            }

            ${child('bottom-center')} ${child('arrow-right')} {
                transform: rotate(180deg);
            }

            ${child('top-center')} {
                align-items: flex-start;
                bottom: 100%;
                left: 50%;
                margin-bottom: 5px;
            }

            ${child('right-center')} {
                align-items: flex-start;
                bottom: 100%;
                left: 50%;
                margin-bottom: 5px;
            }

            ${child('left-center')} {
                align-items: flex-start;
                bottom: 100%;
                left: 50%;
                margin-bottom: 5px;
            }
            ${is('!hide', 'hover')} ${child('trigger')} ${child('top-center')},
            ${is('!hide', 'hover')} ${child('trigger')} ${child('right-center')},
            ${is('!hide', 'hover')} ${child('trigger')} ${child('left-center')},
            ${is('!hide', 'hover')} ${child('trigger')} ${child('bottom-center')},
            ${is('!hide', 'hover')} ${child('trigger')} ${child('top-right')},
            ${is('!hide', 'hover')} ${child('trigger')} ${child('top-left')},
            ${is('!hide', 'hover')} ${child('trigger')} ${child('bottom-right')},
            ${is('!hide', 'hover')} ${child('trigger')} ${child('bottom-left')} {
                visibility: visible;
                opacity: 1;
                transition: opacity var(--tooltip-duration);
                transition-delay: var(--tooltip-delay);
            }

            :host([theme='plot']) ::slotted(p) {
                margin: 0;
            }
        `,
    ] as CSSResultArray;
};
