import {
    BaseElement,
    booleanConverter,
    createClassName,
    defineElement,
    html,
    property,
    state,
} from '@umetrics/sartorius-ui-base';
import { tooltipStyles } from './tooltipStyle';
import { AnimationProperties, TooltipLocation, TooltipTheme } from './tooltipEnums';
import { TooltipProperties } from './tooltipProperties';
import { version } from './metadata';
import {
    TooltipServiceDelay,
    TooltipServiceEvents,
    TooltipServiceOptions,
} from '@umetrics/sartorius-ui-tooltip-service';

@defineElement({ tagName: 'tooltip', styles: tooltipStyles, version })
export class Tooltip extends BaseElement implements TooltipProperties {
    // Public members
    @property({ type: String })
    public message = '';
    @property({ type: TooltipLocation, reflect: true })
    public location = TooltipLocation.TopLeft;
    @property({ type: Boolean, reflect: true, converter: booleanConverter })
    public hide = false;
    @property({ type: Boolean, reflect: true, converter: booleanConverter })
    public controlled = false;
    @property({ type: Object })
    public object: HTMLElement | null = null;
    @property({ type: String, reflect: true, attribute: 'object-id' })
    public objectId: string | null = null;
    @property({ type: TooltipTheme, reflect: true })
    public theme = TooltipTheme.Default;
    @property({ type: Boolean, reflect: true, converter: booleanConverter, attribute: 'own-width' })
    public ownWidth = false;
    @property({ type: TooltipServiceDelay, reflect: true, attribute: 'delay' })
    public delay = TooltipServiceDelay.Short;

    @property({ type: Boolean, reflect: true, converter: booleanConverter })
    get manager() {
        return this._manager;
    }

    set manager(value) {
        const prev = this._manager;
        if (value !== prev) {
            if (value) {
                window.addEventListener(TooltipServiceEvents.Show, this._managerShow);
                window.addEventListener(TooltipServiceEvents.Hide, this._managerHide);
            } else {
                window.removeEventListener(TooltipServiceEvents.Show, this._managerShow);
                window.removeEventListener(TooltipServiceEvents.Hide, this._managerHide);
            }
            this._manager = value;

            this.requestUpdate('manager', prev);
        }
    }

    @state()
    private _hover = false;
    private _initialLocation = TooltipLocation.TopLeft;
    private _container: HTMLDivElement | null = null;
    private _manager = false;
    private _currentTooltipServiceOptions?: TooltipServiceOptions;
    private _contentCount = 0;

    constructor() {
        super();
        this._updatePositions = this._updatePositions.bind(this);
        this._onTooltipMouseenter = this._onTooltipMouseenter.bind(this);
        this._onTooltipMouseleave = this._onTooltipMouseleave.bind(this);
        this._clickObject = this._clickObject.bind(this);
        this._managerHide = this._managerHide.bind(this);
        this._managerShow = this._managerShow.bind(this);
    }

    // Not sure who uses this?
    public async show() {
        await this.updateComplete;
        this._isElementOutViewport(true);
    }

    // Not sure who uses this?
    public close() {
        this._isElementOutViewport(false);
    }

    protected async firstUpdated(changedProperties) {
        super.firstUpdated(changedProperties);

        // Let browser paint before scheduling another update
        await new Promise((r) => setTimeout(r, 0));

        this._findContainer();

        this._initialLocation = this.location;

        if (this.objectId && !this.object && this.parentElement) {
            this.object = this.parentElement.querySelector(`#${this.objectId}`);
        }
    }

    protected update(changedProperties) {
        super.update(changedProperties);

        const prevObjectId = changedProperties.get('objectId');
        if (prevObjectId) {
            if (this.objectId && this.parentElement) {
                this.object = this.parentElement.querySelector(`#${this.objectId}`);
            } else {
                this.object = null;
            }
        }

        const prevObject = changedProperties.get('object');
        if (prevObject !== undefined) {
            if (this.object) {
                this.style.position = 'fixed';
            } else {
                this.style.removeProperty('position');
            }
        }
    }

    protected render() {
        const [baseClassName, rootClasses] = createClassName(this.tagName.toLowerCase(), {
            hide: this.hide || (!this.message && !this.manager),
            hover: this.object ? true : this._hover,
            manager: this.manager,
            'own-width': this.ownWidth,
        });
        const { delay: tooltipDelay, duration: tooltipDuration } = AnimationProperties[this.delay];

        const messageHtml = this.message ? this.message : html` <slot></slot>`;
        const arrowDirection = this.location === 'top-left' || this.location === 'bottom-right' ? 'left' : 'right';

        if (this._contentCount > 0) {
            return html`
                <div
                    class="${rootClasses}"
                    style="--tooltip-delay:${tooltipDelay}s; --tooltip-duration:${tooltipDuration}s"
                >
                    <div
                        class="${baseClassName}-trigger ${baseClassName}-container ${baseClassName}-container-${this
                            .location}"
                    >
                        <div
                            class="${baseClassName}-wrapper ${baseClassName}-${this.location}"
                            @mouseenter="${this._onTooltipMouseenter}"
                            @mouseleave="${this._onTooltipMouseleave}"
                            style="pointer-events: all"
                        >
                            <div class="${baseClassName}-message">${messageHtml}</div>
                            <div class="${baseClassName}-arrow-${arrowDirection}"></div>
                        </div>
                    </div>
                </div>
            `;
        } else return null;
    }

    private _onTooltipMouseenter() {
        if (this._currentTooltipServiceOptions && this._currentTooltipServiceOptions.root) {
            this._currentTooltipServiceOptions.root.dispatchEvent(
                new CustomEvent(TooltipServiceEvents.Hover, {
                    detail: {},
                })
            );
        }
    }

    private _onTooltipMouseleave() {
        if (this._currentTooltipServiceOptions && this._currentTooltipServiceOptions.root) {
            this._currentTooltipServiceOptions.root.dispatchEvent(
                new CustomEvent(TooltipServiceEvents.Unhover, {
                    detail: {},
                })
            );
        }
    }

    private _clickObject() {
        this._isElementOutViewport(false);
    }

    private _isElementOutViewport(hover) {
        this._hover = hover;

        if (this.object != null && this.shadowRoot) {
            if (this._hover) {
                this._updatePositions();
                document.addEventListener('scroll', this._updatePositions);
                this.object.addEventListener('click', this._clickObject);
            }
            return;
        }

        const element = this.shadowRoot!.querySelector('[class*=wrapper]') as HTMLDivElement;

        if (element) {
            if (this.manager && this._currentTooltipServiceOptions && this.location === TooltipLocation.Auto) {
                const triggerRect = this._currentTooltipServiceOptions.object.getBoundingClientRect();
                const { width: itemsWidth, height: itemsHeight } = element.getBoundingClientRect();

                const { spaceAbove, spaceBelow, spaceToLeft, spaceToRight } = this._calculateSpace(triggerRect);
                const margin = 10;
                const w2 = itemsWidth / 2 + margin;
                const valign = this._getValign(spaceAbove, itemsHeight, margin, spaceBelow);
                const halign = this._getHalign(spaceToLeft, w2, spaceToRight);
                this._updateLocation(valign, halign);
            } else {
                // Deprecated below...
                this._updateLocationNonManaged(element);
            }
        }
    }

    private _updateLocation(valign: string, halign: string) {
        if (valign && halign) {
            this.location = (valign + '-' + halign) as TooltipLocation;
        } else {
            this.location = TooltipLocation.TopLeft;
        }
    }

    private _updateLocationNonManaged(element: HTMLDivElement) {
        this.location = this.location === TooltipLocation.Auto ? TooltipLocation.TopLeft : this.location;
        const { topOutViewport, leftOutViewport, rightOutViewport, bottomOutViewport } = this._outViewport(
            element.getBoundingClientRect()
        );

        if (topOutViewport) {
            this.location = TooltipLocation.BottomLeft;
        }

        if (topOutViewport && leftOutViewport) {
            this.location = TooltipLocation.BottomRight;
        }

        if (rightOutViewport || (topOutViewport && rightOutViewport)) {
            this.location = TooltipLocation.BottomLeft;
        }

        if (bottomOutViewport) {
            this.location = TooltipLocation.TopLeft;
        }

        if ((bottomOutViewport && leftOutViewport) || leftOutViewport) {
            this.location = TooltipLocation.TopRight;
        }

        if (topOutViewport && rightOutViewport && leftOutViewport && bottomOutViewport) {
            this.location = this._initialLocation;
        }
    }

    private _getHalign(spaceToLeft: number, w2: number, spaceToRight: number) {
        if (spaceToLeft < w2 && spaceToRight >= w2) {
            return 'right';
        } else if (spaceToLeft >= w2 && spaceToRight < w2) {
            return 'left';
        }
        return 'right';
    }

    private _getValign(spaceAbove: number, itemsHeight: number, margin: number, spaceBelow: number) {
        if (spaceAbove > itemsHeight + margin) {
            return 'top';
        } else if (spaceBelow > itemsHeight + margin) {
            return 'bottom';
        } else {
            return 'top';
        }
    }

    private _calculateSpace(triggerRect: DOMRect) {
        const windowWidth = window.innerWidth || document.documentElement.clientWidth || document.body.clientWidth;
        const windowHeight = window.innerHeight || document.documentElement.clientHeight || document.body.clientHeight;

        const spaceAbove = triggerRect.top;
        const spaceBelow = windowHeight - triggerRect.bottom;
        const spaceToLeft = triggerRect.left;
        const spaceToRight = windowWidth - triggerRect.right;

        return { spaceAbove, spaceBelow, spaceToLeft, spaceToRight };
    }

    private _findContainer() {
        if (this._container === null) {
            this._container = document.body.querySelector('div[class="sartorius-ui-tooltips"]');
            if (this._container === null) {
                this._container = document.createElement('div');
                this._container.className = 'sartorius-ui-tooltips';
                this._container.style.position = 'absolute';
                this._container.style.zIndex = '2000';
                document.body.appendChild(this._container);
            }
        }
        return this._container;
    }

    private async _updatePositions() {
        await this.updateComplete;
        await this._updateInnerPositions(this.object);
    }

    private async _updateInnerPositions(obj) {
        if (obj) {
            const objectRect = obj.getBoundingClientRect();
            this.style.top = objectRect.y + 'px';
            this.style.left = objectRect.x + 'px';
            this.style.width = objectRect.width + 'px';
            this.style.height = objectRect.height + 'px';
            this.style.pointerEvents = 'none';
        }
    }

    private _outViewport(rect) {
        return {
            topOutViewport: rect.top < 0,
            leftOutViewport: rect.left < 0,
            rightOutViewport: rect.right > window.innerWidth,
            bottomOutViewport: rect.bottom > window.innerHeight,
        };
    }

    private _managerHide(e) {
        this._hide(e.detail.root);
    }

    private _hide(root) {
        if (this._currentTooltipServiceOptions && this._currentTooltipServiceOptions.root === root) {
            this._hover = false;
            while (this.firstChild && this.lastChild) {
                this.removeChild(this.lastChild);
            }
            if (this._currentTooltipServiceOptions && this._currentTooltipServiceOptions.hideCallback) {
                this._currentTooltipServiceOptions.hideCallback();
            }
            this._currentTooltipServiceOptions = undefined;
        }
    }

    private async _managerShow(e) {
        if (this._currentTooltipServiceOptions) {
            this._hide(this._currentTooltipServiceOptions.root);
        }
        if (e.detail && e.detail.root) {
            this._currentTooltipServiceOptions = e.detail;
            this.location = e.detail.location || 'auto';
            this.ownWidth = e.detail.ownWidth || false;
            this.delay = e.detail.delay || TooltipServiceDelay.Short;
            this._copyChildren(e.detail.root, this);

            ['size', 'resizeable', 'draggable'].forEach((s) => {
                if (e.detail[s] !== undefined) {
                    this[s] = e.detail[s];
                }
            });
            await this._updateInnerPositions(e.detail.root.object);
            this._isElementOutViewport(true);
            if (this._currentTooltipServiceOptions && this._currentTooltipServiceOptions.showCallback) {
                this._currentTooltipServiceOptions.showCallback();
            }
        }
    }

    private _copyChildren(src, dest) {
        this._contentCount = src.childNodes.length;
        let i = 0;
        while (src.childNodes && i < src.childNodes.length) {
            dest.appendChild(src.childNodes[i].cloneNode(true));
            ++i;
        }
    }
}
