export type TooltipServiceTrigger = 'mouseover' | 'mouseout' | 'click' | 'none';

// Same as redundant TooltipLocation
export enum TooltipServiceLocation {
    Auto = 'auto',
    TopRight = 'top-right',
    TopLeft = 'top-left',
    BottomRight = 'bottom-right',
    BottomLeft = 'bottom-left',
    TopCenter = 'top-right',
    RightCenter = 'top-right',
    LeftCenter = 'top-left',
    BottomCenter = 'bottom-right',
}

export enum TooltipServiceDelay {
    None,
    Short,
    Long,
}

export interface TooltipServiceProperties {
    show?: boolean;
    disabled?: boolean;
    location?: TooltipServiceLocation | string;
    delay?: TooltipServiceDelay;
    object?: HTMLElement | null;
    objectId?: string | null;

    ownWidth?: boolean;

    triggerShow?: TooltipServiceTrigger;
    triggerHide?: TooltipServiceTrigger;

    onOpen?: any;
    onClose?: any;
    hoverable?: boolean;
}
